/**
 *
 * Link
 *
 * @author Takuto Yanagida
 * @version 2021-12-06
 *
 */


'use strict';

window['NACSS'] = window['NACSS'] || {};


(function (NS) {

	{
		/**
		 *
		 * Type
		 *
		 * @author Takuto Yanagida
		 * @version 2021-12-06
		 *
		 */
		
		
		function apply(as, opts = {}) {
			opts = Object.assign({
				styleLinkImage   : ':ncLinkImage',
				styleLinkSimple  : ':ncLinkSimple',
				styleLinkAnchor  : ':ncLinkAnchor',
				styleLinkExternal: ':ncLinkExternal',
				styleLinkFile    : ':ncLinkFile',
				allowedClasses   : ALLOWED_CLASSES,
				extensionTable   : EXT_TABLE,
				isUrlExternal    : isUrlExternal,
			}, opts);
		
			for (const a of as) {
				addClass(a, a.getAttribute('href'), opts);
			}
		}
		
		function applyByUrl(as, opts = {}) {
			opts = Object.assign({
				styleLinkAnchor  : ':ncLinkAnchor',
				styleLinkExternal: ':ncLinkExternal',
				styleLinkFile    : ':ncLinkFile',
				extensionTable   : EXT_TABLE,
				isUrlExternal    : isUrlExternal,
			}, opts);
		
			for (const a of as) {
				addClassByUrl(a, a.getAttribute('href'), opts);
			}
		}
		
		const ALLOWED_CLASSES = [
			'alignleft',
			'aligncenter',
			'alignright',
			'size-thumbnail',
			'size-small',
			'size-medium-small',
			'size-medium',
			'size-medium-large',
			'size-medium_large',
			'size-large',
			'size-full'
		];
		
		const EXT_TABLE = {
			doc : 'doc',
			docx: 'doc',
			xls : 'xls',
			xlsx: 'xls',
			ppt : 'ppt',
			pptx: 'ppt',
			pdf : 'pdf'
		};
		
		
		// -------------------------------------------------------------------------
		
		
		function addClass(a, url, opts) {
			if (isLinkImage(a, opts)) {
				setClass(a, opts.styleLinkImage);
			} else {
				if (isLinkEmpty(a)) {
					addClassByUrl(a, url, opts);
				} else if (isLinkSimple(a)) {
					setClass(a, opts.styleLinkSimple);
					if (isLinkSimpleUrl(a, url)) {
						setClass(a, opts.styleLinkSimple, true, 'url');
					}
					addClassByUrl(a, url, opts);
				}
			}
		}
		
		function isLinkImage(a, opts) {
			if (a.className) {
				for (const c of a.className.split(' ')) {
					if (!opts.allowedClasses.includes(c)) return false;
				}
			}
			let ok = false;
			for (const c of a.childNodes) {
				if (c.nodeType === 1) {  // ELEMENT_NODE
					if (!ok && c.tagName === 'IMG') {
						ok = true;
					} else {
						ok = false;
						break;
					}
				}
			}
			return ok;
		}
		
		function isLinkSimple(a) {
			if (a.className) return false;
			const cs = a.childNodes;
			if (cs.length === 0) return false;
			for (const c of cs) {
				if (c.className) return false;
				if (c.nodeType === 1) {  // ELEMENT_NODE
					if (c.tagName !== 'BR' && !getComputedStyle(c).display.includes('inline')) {
						return false;
					}
				}
			}
			return true;
		}
		
		function isLinkEmpty(a) {
			return !a.className && a.childNodes.length === 0;
		}
		
		function isLinkSimpleUrl(a, url) {
			return url !== null && url !== '' && a.childNodes.length && a.innerHTML.trim() === url;
		}
		
		
		// -------------------------------------------------------------------------
		
		
		function addClassByUrl(a, url, opts) {
			const outIsUp = [false];
			if (isUrlAnchor(url, a, outIsUp)) {
				setClass(a, opts.styleLinkAnchor, true, outIsUp[0] ? 'up' : '');
			} else if (opts.isUrlExternal(url)) {
				setClass(a, opts.styleLinkExternal);
			}
			const t = getFileType(url, opts.extensionTable);
			if (t) {
				setClass(a, opts.styleLinkFile);
				setClass(a, opts.styleLinkFile, true, t);
			}
		}
		
		function isUrlExternal(url) {
			if (url !== null && url !== '') {
				if (!url.startsWith(`${location.protocol}//${location.host}`)) {
					if (url.match(/^https?:\/\//) || url.match(/^\/\//)) {
						return true;
					}
				}
			}
			return false;
		}
		
		function getFileType(url, extTab) {
			if (url !== null && url !== '' && !url.endsWith('/')) {
				const d = url.indexOf('//');
				if (d !== -1) {
					const s = url.indexOf('/', d + 2);
					url = (s === -1) ? '' : url.substring(s + 1);
				}
				const p = url.lastIndexOf('.');
				if (p !== -1) {
					const ext = url.substring(p + 1).toLowerCase();
					return extTab[ext] ?? null;
				}
			}
			return null;
		}
		
		NS.linkApply      = apply;
		NS.linkApplyByUrl = applyByUrl;

		NS.linkIsLinkImage = isLinkImage;
	}

	{
		/**
		 *
		 * Anchor Scroll
		 *
		 * @author Takuto Yanagida
		 * @version 2021-11-29
		 *
		 */
		
		
		const DURATION = 400;
		
		let isScrolling = false;
		const anchorWithListener = new Set();
		
		function initialize(as, opts = {}) {
			opts = Object.assign({
				duration        : DURATION,
				observedSelector: null,
			}, opts);
		
			for (const a of as) {
				apply(a, opts);
			}
			document.addEventListener('wheel', () => { isScrolling = false; });
		
			if (opts['observedSelector']) {
				const oes = document.querySelectorAll(opts['observedSelector']);
				if (oes.length) {
					const mo = new MutationObserver(rs => {
						for (const r of rs) {
							for (const n of r.addedNodes) {
								if (n.nodeType === 1 && n.tagName === 'A') {
									apply(n, opts);
								}
							}
						}
					});
					for (const oe of oes) {
						mo.observe(oe, {
							childList: true,
							subtree  : true,
						});
					}
				}
			}
		}
		
		function apply(a, opts) {
			if (!anchorWithListener.has(a)) {
				function onClick(e) { doClick(e, opts); }
				if (a.href === '#' || a.href === '#top' || isUrlAnchor(a.href)) {
					a.addEventListener('click', onClick);
					anchorWithListener.add(a);
				}
			}
		}
		
		function doClick(e, opts) {
			if (e.button !== 0 || e.ctrlKey || e.shiftKey || e.altKey || e.metaKey) return;
			const href = e.currentTarget.getAttribute('href');
			const idx  = href.indexOf('#');
			if (idx !== -1) {
				smoothScrollToHash(e, href.substring(idx), opts.duration);
			}
		}
		
		function smoothScrollToHash(e, hash, duration = DURATION) {
			const top = (hash === null || hash === '' || hash === '#' || hash === '#top');
			const tar = top ? document.documentElement : document.getElementById(hash.substring(1));
			if (tar && !window.matchMedia('(prefers-reduced-motion: reduce)').matches) {
				e.stopPropagation();
				e.preventDefault();
		
				smoothScrollToElement(tar, duration);
				if (hash !== window.location.hash) {
					history.pushState(null, null, (hash === '#top' ? '' : hash));
				}
			}
		}
		
		
		// -----------------------------------------------------------------------------
		
		
		function smoothScrollToElement(tar, duration = DURATION, focus = true) {
			const start = window.pageYOffset;
			let posY = getScrollPos(tar);
			let wh   = document.documentElement.offsetHeight;
		
			let timeStart;
			isScrolling = true;
			requestAnimationFrame((time) => { timeStart = time; loop(time); });
		
			function loop(time) {
				if (!isScrolling) return;
				if (wh !== document.documentElement.offsetHeight) {  // For delay loading
					posY = getScrollPos(tar);
					wh   = document.documentElement.offsetHeight;
				}
				window.scrollTo(0, easing(time - timeStart, start, posY, duration));
				if (time - timeStart < duration) {
					requestAnimationFrame(loop)
				} else {
					window.scrollTo(0, getScrollPos(tar));
					setTimeout(() => window.scrollTo(0, getScrollPos(tar)), 50);
					if (focus && tar && tar !== document.documentElement) {
						tar.focus();
					}
					isScrolling = false;
				}
			}
		}
		
		function getScrollPos(tar) {
			const spt = parseInt(getComputedStyle(document.documentElement).scrollPaddingTop);
			return tar.getBoundingClientRect().top + window.pageYOffset - spt;
		}
		
		function easing(t, b, c, d) {
			t /= d / 2;
			if (t < 1) return (c - b) / 2 * t * t + b;
			t--;
			return -(c - b) / 2 * (t * (t - 2) - 1) + b;
		}
		
		NS.linkAnchorScroll = initialize;

		NS.smoothScrollToElement = smoothScrollToElement;
	}

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Common Functions
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-10
	 *
	 */
	
	
	function isUrlAnchor(url, a = null, outIsUp = null) {
		if (url !== null && url !== '') {
			const p = url.indexOf('#');
			if (p !== -1) {
				const id = url.substring(p + 1);
				if (id.length) {
					const t = document.getElementById(id);
					if (t !== null) {
						if (
							p === 0 ||
							url.substring(0, p) === location.href.replace(location.hash ? location.hash : '#', '')
						) {
							if (a && outIsUp) {
								const from = a.getBoundingClientRect().top;
								const to   = t.getBoundingClientRect().top;
								if (to < from) outIsUp[0] = true;
							}
							return true;
						}
					}
				}
			}
		}
		return false;
	}
	

})(window['NACSS']);
